// src/app/components/page.tsx
'use client'

import { useQuery } from '@tanstack/react-query'
import { Button } from '@/components/ui/button'
import { MaterialCard } from '@/components/materials/MaterialCard'
import { fetchMaterials, fetchCategories } from '@/lib/api'

interface Material {
    id: number
    name: string
    units: string
    pricePerUnit: number
    imageUrl: string | null
    categoryId: number
}

interface Category {
    id: number
    name: string
    description: string | null
}

export default function ComponentsPage() {
    const { data: materials, isLoading: isLoadingMaterials } = useQuery<Material[]>({
        queryKey: ['materials'],
        queryFn: fetchMaterials
    })

    const { data: categories, isLoading: isLoadingCategories } = useQuery<Category[]>({
        queryKey: ['categories'],
        queryFn: fetchCategories
    })

    const materialsByCategory = materials?.reduce((acc, material) => {
        const categoryId = material.categoryId
        if (!acc[categoryId]) {
            acc[categoryId] = []
        }
        acc[categoryId].push(material)
        return acc
    }, {} as Record<number, Material[]>) || {}

    const handleEdit = (id: number) => {
        alert(`Edit material ${id}`)
    }

    const handleDelete = (id: number) => {
        alert(`Delete material ${id}`)
    }

    if (isLoadingMaterials || isLoadingCategories) {
        return <div className="text-center p-6">Loading...</div>
    }

    return (
        <div>
            <div className="mb-6 flex justify-between items-center">
                <h1 className="text-2xl font-semibold text-gray-900">Components</h1>
                <Button onClick={() => alert('Add new material')}>
                    Add Component
                </Button>
            </div>

            <div className="space-y-8">
                {categories?.map((category) => {
                    const categoryMaterials = materialsByCategory[category.id] || []

                    if (categoryMaterials.length === 0) {
                        return null
                    }

                    return (
                        <div key={category.id} className="space-y-4">
                            <div className="border-b pb-2">
                                <h2 className="text-xl font-medium text-gray-900">
                                    {category.name}
                                    <span className="ml-2 text-sm text-gray-500">
                                        ({categoryMaterials.length} items)
                                    </span>
                                </h2>
                                {category.description && (
                                    <p className="text-sm text-gray-500 mt-1">{category.description}</p>
                                )}
                            </div>

                            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                                {categoryMaterials.map((material) => (
                                    <MaterialCard
                                        key={material.id}
                                        {...material}
                                        onEdit={handleEdit}
                                        onDelete={handleDelete}
                                    />
                                ))}
                            </div>
                        </div>
                    )
                })}

                {Object.keys(materialsByCategory).length === 0 && (
                    <div className="text-center p-6 bg-white rounded-lg shadow">
                        <p className="text-gray-500">No materials found</p>
                    </div>
                )}
            </div>
        </div>
    )
}
